import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
// import Button from "@material-ui/core/Button";
// import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";

const GREEN = "#06bfad";
const PINK = "#e03268";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    "& .MuiStepIcon-root.MuiStepIcon-active": {
      color: PINK,
      fontWeight: "bold",
      transition: "all 0.5s ease-in-out",
      content: "none",
      "&:hover": {
        color: GREEN,
      },
    },
  },
}));

function getSteps() {
  return [
    "Watching online video lectures",
    "1:1 Live doubt discussions",
    "Solving quizzes",
    "Challenging assignments",
    "Review and feedback",
  ];
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return `Students watch animated explainer videos designed keeping young kids in mind.`;
    case 1:
      return `Every doubt and difficulty can be solved by opting in a 1:1 live doubt discussion session for as long as the course is not completed. 
                    This will make sure the students are able to freely clear any doubt without any peer pressure or judgement`;
    case 2:
      return `Every tutorial is accompanied with challenging quizzes to make sure the kids has a high level of understanding`;
    case 3:
      return `After learning a crucial concept, the child is presented with a new problem and needs to think of creative ways to solve the assignments.`;
    case 4:
      return `Every assignment submission is reviewed by expert faculties and appropriate feedback and help is provided to make sure the child learns in the best possible way.`;
    default:
      return "Unknown step";
  }
}

export default function AboutStepper() {
  const classes = useStyles();
  const steps = getSteps();

  return (
    <div className={classes.root}>
      <Stepper orientation="vertical">
        {steps.map((label, index) => (
          <Step active={true} key={label}>
            <StepLabel>
              <Typography variant="h6">
                <strong>{label}</strong>
              </Typography>
            </StepLabel>
            <StepContent>
              <Typography variant="subtitle2">
                {getStepContent(index)}
              </Typography>
            </StepContent>
          </Step>
        ))}
      </Stepper>
    </div>
  );
}
